.contactPage{
  height:350px;
  background:-webkit-gradient(linear,left top, left bottom,from(rgb(97, 201, 144)),to(rgb(207, 149, 40)));
  background:-webkit-linear-gradient(rgb(97, 201, 144),rgb(207, 149, 40));
  background:-o-linear-gradient(rgb(97, 201, 144),rgb(207, 149, 40));
  background:linear-gradient(rgb(97, 201, 144),rgb(207, 149, 40));
  padding-top:1rem;
}
.cContactPage h1{
  text-align: center;
  font-family: 'Lobster', cursive;
  margin-top:0;
  padding-top:1rem;
}
.wrap{
  width:40%;
  height:400px;
  margin-top:1rem;
  margin:0 auto;
}
.links a{
  color:white;
  font-weight: 700;
  text-decoration: none;
}
.links a:hover{
  color:tomato;
  
}
@media(max-width:450px){
  .contactPage{
    font-size:0.8rem;
  }
  .wrap{
    width:70%;
    margin-left:25%;
  }
}



