.CalorieApp{
  background:-webkit-gradient(linear,left top, left bottom,from(rgb(97, 201, 144)),to(rgb(40, 168, 207)));
  background:-webkit-linear-gradient(rgb(97, 201, 144),rgb(40, 168, 207));
  background:-o-linear-gradient(rgb(97, 201, 144),rgb(40, 168, 207));
  background:linear-gradient(rgb(97, 201, 144),rgb(40, 168, 207));
}
.CalorieApp h1{
  margin:0;
  padding:1rem 0;
  text-align: center;
  font-family: 'Lobster', cursive;
}
.CalorieApp p{
  text-align: center;
}
.BarcodeForm{
  text-align: center;
}
.BarcodeInput{
   width:40%;
   height:1.5rem;
   margin:1rem auto;
}
.submitButton{
  border:none;
  padding:1rem;
  color:white;;
  background:tomato;
  cursor:pointer;
  font-size:1.2rem;
}
.productImg{
  display:block;
  margin:0 auto;
  max-width: 100%;
  max-height:300px;
  
  
}
.productImgNutrition{
  display:block;
  margin:1rem auto;
  max-width: 100%;
  max-height:150px;
}
p{
  font-weight:bold;
  text-align: center;
  margin-right:2rem;
  
}
.Info img{border: 3px solid grey;
  max-width:100%;
  
}
.Info{
  margin:1rem auto 0;
  width:80%;
  height:600px;
  background:#222222;
  -webkit-box-shadow: 4px 4px 4px black;
          box-shadow: 4px 4px 4px black;
  color:white;
  padding:1rem;
}
.Info ul{
  display:block;
  margin:0 35%;

}
.Info li{
  list-style: none;
  margin:0 auto;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  
}
.Info li span{
  color:dodgerblue;
  font-weight:700;
}
.name{
  font-size:1.5rem;
  font-family:'Lobster',cursive;
}
.HowTo{
  max-width: 100%;
  display:block;
  margin:0 auto;
  width:200px;
  height:200px;
  -webkit-transition: all 0.5s ease-in;
  -o-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}
.HowTo:hover{
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-filter:hue-rotate(20deg);
          filter:hue-rotate(20deg)
}
.description{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.description div{
  margin:0 1rem;
}
.description div img{
 max-width:100%;
 height:auto;
 width:170px;
}
.error{
  background-color:tomato;
  color:white;
  text-align: center;
  height:3rem;
  font-size:1rem;
  padding:0.5rem;
}
.imgDiv{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
}
/* spinner */
.lds {
  display: block;
  width: 64px;
  height: 64px;
  margin:1rem auto;
}
.lds:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: lds 1.2s linear infinite;
          animation: lds 1.2s linear infinite;
}

@-webkit-keyframes lds{
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds{
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media(max-width:1000px){
  .CalorieApp{
    padding-left:5.5rem;
  }
  .error{
   padding-left:1rem;
   height:5rem;
  }
  .Info ul{
    margin:0 20%;
  }
}
@media(max-width:600px){
  .CalorieApp{
    padding-left:4.3rem;
  }
  .error{
   /* padding-left:1rem; */
   height:5rem;
  }
  .description div img{
    width:130px;
    max-width:100%;
    height:auto;
   }
   .Info ul{
    margin:0 5%;
  }
  .Info{
    height:780px;
    background:#222222;
  }
}


